import { IFilters, IAuditTrail } from './AuditTrail.types';
import { Box } from '@mui/system';
import FilterInput, { IFilterInputProps } from '../../components/FilterInput/FilterInput';
import FilterSelect, { SelectProps } from '../../components/FilterInput/FilterSelect';
import { Button, Stack } from '@mui/material';
import DateRange from '../../components/FilterInput/DateRange';
import { RangePickerProps } from 'antd/lib/date-picker'
import {Table} from '../../components/Table';
import { ColumnType } from '../../components/Table/Table.types';
import { useState } from 'react';
import { FilterType } from 'react-table';
import moment from 'moment'
import { Search } from '@mui/icons-material';

export default function AuditTrail(props: IAuditTrail) {
  const [filters, setFilters] = useState<IFilters>({
    user: null,
    role: null,
    time: null
  })

  let data = [
    { user: 'Keri Hudson', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '30 min ago' },
    { user: 'Randall Hurley', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Gayle Gaines', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Barrera Ramsey', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Keri Hudson', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Keri Hudson', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Keri Hudson', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '30 min ago' },
    { user: 'Randall Hurley', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Gayle Gaines', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Barrera Ramsey', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Keri Hudson', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Keri Hudson', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Keri Hudson', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '30 min ago' },
    { user: 'Randall Hurley', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Gayle Gaines', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Barrera Ramsey', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
    { user: 'Keri Hudson', role: 'Admin', action: 'Lorem ipsum dolor sit amet,', type: 'Ut enim ad minim veniam', status: 'Ut enim ad minim veniam', time: '09/11/21' },
  ]

  const rangeFilterFunction: FilterType<typeof data[number]> = (rows, ids, filterValue) => {
    if(!filterValue || (!filterValue[0] && !filterValue[1])) {
      return rows
    }

    return rows.filter(row => {
      return ids.some(id => {
        const rowValue = row.values[id]
        return moment(rowValue, 'DD-MM-YY').isBetween(filterValue[0], filterValue[1])
      })
    })
  }

  let columns: ColumnType<typeof data[number]>[] = [
    { Header: 'User', accessor: 'user' },
    { Header: 'Role', accessor: 'role' },
    { Header: 'Action', accessor: 'action'},
    { Header: 'Type', accessor: 'type' },
    { Header: 'Status', accessor: 'status' },
    { Header: 'Time', accessor: 'time', filter: rangeFilterFunction }
  ]

  const onRoleChange: SelectProps["onChange"] = (value) => {
    setFilters({
      ...filters,
      role: value
    })
  }

  const onNameChange: IFilterInputProps<typeof filters.user>["onChange"] = (e) => {
    setFilters({
      ...filters,
      user: e.target.value
    })
  }

  const onDateRangeChange: RangePickerProps["onChange"]  = (values, formatString) => {
    console.log(values, formatString)
    if(values && values[0] && values[1]) {
      setFilters({
        ...filters,
        time: [values[0].format('YYYY-MM-DD'), values[1].format('YYYY-MM-DD')]
      })
    } else {
      setFilters({
        ...filters,
        time: null
      })
    }
  }
  
  return (
    <Box>
      <Box mb={2} display='flex' justifyContent='space-between' alignItems='flex-end'>
        <Stack direction='row' alignItems='center' spacing={3.5}>
          <FilterInput label="Select Product" value={filters.user} onChange={onNameChange} endAdornment={<Search fontSize='large' color='action'/>} />
          <FilterSelect label="Select Role" value={filters.role} onChange={onRoleChange}>
            <option label='Admin' value='Admin'>
              Admin
            </option>
            <option label='User' value='user'>
              User
            </option>
            <option label='SuperAdmin' value='superadmin'>
              SuperAdmin
            </option>
          </FilterSelect>
          <DateRange label="Select Date" onChange={onDateRangeChange}/>
        </Stack>
        <Box pl={2} justifySelf='flex-end'>
          <Button variant='contained'>Search</Button>
        </Box>
      </Box>
      <Table columns={columns} data={data} filters={filters} />
    </Box>
  );
}