import React, { PropsWithChildren, useEffect, useMemo } from "react"
import TableContainer from "@mui/material/TableContainer"
import TableHead from "@mui/material/TableHead"
import {
	StyledPaper,
	StyledTableCell,
	StyledTableRow,
	StyledTableBody,
	StyledTable,
	StyledHeaderRow,
	PaginationContainer,
	StyledPagination,
	StyledPaginationItem,
	StyledSortIcon
} from "./Table.styles"
import { ITable } from "./Table.types"
import { Box } from "@mui/system"
import { Typography } from "@mui/material"
import {
	ArrowForward,
	ArrowBack,
	SyncAlt,
	ExpandLess,
	ExpandMore
} from "@mui/icons-material"
import { useTable, useFilters, usePagination, useSortBy } from "react-table"
import typedMemo from "../../utils/typedMemo"

function Table<DataType extends object = {}>(
	props: PropsWithChildren<ITable<DataType>>
) {
	const {
		columns,
		data,
		filters,
		rowsPerPage,
		useSorting,
		hiddenColumns,
		metaData,
		setPage,
		page: pageNo
	} = props

	const memoizedColumns = useMemo(() => columns, [])
	const memoizedData = useMemo(() => data, [data])

	//add plugins to the table
	//order of adding is important here check docs
	const plugins: any[] = [useFilters]
	if (useSorting) {
		plugins.push(useSortBy)
	}
	plugins.push(usePagination)

	const tableInstance = useTable<DataType>(
		{
			columns: memoizedColumns,
			data: memoizedData,

			initialState: {
				pageSize: rowsPerPage || 10,
				pageIndex: 0,
				hiddenColumns
			},

			//used for server side pagination
			manualPagination: true,
			pageCount: metaData?.page_count,
			autoResetPage: false
		},
		...plugins
	)

	const {
		getTableProps,
		getTableBodyProps,
		headerGroups,
		prepareRow,
		setFilter,
		page,
		pageCount,
		gotoPage,
		setPageSize,
		canNextPage,
		state: { pageIndex, pageSize }
	} = tableInstance

	console.log("tableInstance", tableInstance)

	useEffect(() => {
		Object.entries(filters).forEach(([key, value]) => setFilter(key, value))
	}, [filters])

	useEffect(() => {
		if (rowsPerPage) {
			setPageSize(rowsPerPage)
			//reset the page to 0 for every row entries change
			gotoPage(0)
		}
	}, [rowsPerPage])

	const emptyRows = Math.abs(pageSize - page.length)
	const startRowNo = pageIndex * pageSize + 1
	//for last page the final value would be the total records
	const endRowNo = !canNextPage
		? metaData?.total_records
		: pageIndex * pageSize + pageSize

	return (
		<Box>
			<TableContainer component={StyledPaper}>
				<StyledTable
					sx={{ minWidth: 700 }}
					aria-label="customized table"
					{...getTableProps()}
				>
					<TableHead>
						{
							// Loop over the header rows
							headerGroups.map(headerGroup => (
								// Apply the header row props
								<StyledHeaderRow {...headerGroup.getHeaderGroupProps()}>
									{
										// Loop over the headers in each row
										headerGroup.headers.map((column, id) => (
											// Apply the header cell props
											<StyledTableCell
												{...column.getHeaderProps(
													useSorting ? column.getSortByToggleProps() : undefined
												)}
											>
												<Box
													display="flex"
													width="100%"
													justifyContent="space-between"
													alignItems="center"
												>
													<>
														{
															// Render the header
															column.render("Header")
														}
														{useSorting && (
															<Box component="span">
																{column.isSorted ? (
																	column.isSortedDesc ? (
																		<ExpandLess />
																	) : (
																		<ExpandMore />
																	)
																) : column.canSort ? (
																	<StyledSortIcon />
																) : null}
															</Box>
														)}
													</>
												</Box>
											</StyledTableCell>
										))
									}
								</StyledHeaderRow>
							))
						}
					</TableHead>
					<StyledTableBody {...getTableBodyProps()}>
						{
							// Loop over the table rows
							page.map(row => {
								// Prepare the row for display
								prepareRow(row)
								return (
									// Apply the row props
									<StyledTableRow {...row.getRowProps()}>
										{
											// Loop over the rows cells
											row.cells.map(cell => {
												// Apply the cell props
												return (
													<StyledTableCell {...cell.getCellProps()}>
														<Typography>
															<>{cell.render("Cell")}</>
														</Typography>
													</StyledTableCell>
												)
											})
										}
									</StyledTableRow>
								)
							})
						}
						{new Array(emptyRows).fill(0).map(() => (
							<StyledTableRow sx={() => ({ backgroundColor: "transparent" })}>
								<StyledTableCell>
									<Typography>&nbsp;</Typography>
								</StyledTableCell>
							</StyledTableRow>
						))}
					</StyledTableBody>
				</StyledTable>
				<PaginationContainer>
					<Box>
						<Typography>
							Showing {startRowNo} to {endRowNo} of {metaData?.total_records}
						</Typography>
					</Box>
					<Box>
						<StyledPagination
							color="primary"
							count={metaData?.page_count || pageCount}
							shape="rounded"
							page={pageNo}
							onChange={(e, pNo) => {
								console.log(pNo, "Page is here")
								//manuaaly invoke react-table page start with 0 so -1 to the page
								gotoPage(pNo - 1)
								//Manually Invoke setPage offset start with 1
								setPage?.(pNo)
							}}
							renderItem={item => (
								<StyledPaginationItem
									{...item}
									components={{ next: ArrowForward, previous: ArrowBack }}
								/>
							)}
						/>
					</Box>
				</PaginationContainer>
			</TableContainer>
		</Box>
	)
}

Table.defaultProps = {
	hiddenColumns: []
}

export default typedMemo(Table)
